'use client';
import { useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/contexts/AuthContext';

export default function LoginPage() {
    const { user, loading, signInWithGoogle } = useAuth();
    const router = useRouter();

    useEffect(() => {
        // Redirect if already logged in
        if (user && !loading) {
            router.push('/');
        }
    }, [user, loading, router]);

    const handleGoogleSignIn = async () => {
        try {
            await signInWithGoogle();
            router.push('/');
        } catch (error) {
            alert('Error al iniciar sesión: ' + error.message);
        }
    };

    if (loading) {
        return (
            <div style={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                minHeight: '100vh',
                background: '#fafafa'
            }}>
                <p style={{ fontSize: '1.1rem', color: '#667085' }}>Cargando...</p>
            </div>
        );
    }

    return (
        <div style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            minHeight: '100vh',
            background: '#fafafa'
        }}>
            <div style={{
                background: '#ffffff',
                border: '1px solid #f1f5f9',
                borderRadius: '32px',
                padding: '4rem 3rem',
                boxShadow: '0 1px 3px 0 rgba(0, 0, 0, 0.02), 0 20px 25px -5px rgba(0, 0, 0, 0.05)',
                textAlign: 'center',
                maxWidth: '420px',
                width: '100%'
            }}>
                <h1 style={{
                    fontSize: '2rem',
                    color: '#101828',
                    marginBottom: '0.75rem',
                    letterSpacing: '-0.02em',
                    fontWeight: '600'
                }}>
                    Sistema de Cotizaciones
                </h1>
                <p style={{
                    color: '#667085',
                    marginBottom: '3rem',
                    fontSize: '1rem'
                }}>
                    Inicia sesión para acceder a tu cuenta
                </p>

                <button
                    onClick={handleGoogleSignIn}
                    className="btn"
                    style={{
                        width: '100%',
                        background: '#ffffff',
                        color: '#101828',
                        border: '1px solid #e2e8f0',
                        padding: '0.875rem 1.5rem',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        gap: '12px',
                        fontSize: '1rem',
                        fontWeight: '500'
                    }}
                >
                    <svg width="18" height="18" viewBox="0 0 18 18" xmlns="http://www.w3.org/2000/svg">
                        <path d="M17.64 9.2c0-.637-.057-1.251-.164-1.84H9v3.481h4.844c-.209 1.125-.843 2.078-1.796 2.717v2.258h2.908c1.702-1.567 2.684-3.874 2.684-6.615z" fill="#4285F4" />
                        <path d="M9 18c2.43 0 4.467-.806 5.956-2.184l-2.908-2.258c-.806.54-1.837.86-3.048.86-2.344 0-4.328-1.584-5.036-3.711H.957v2.332C2.438 15.983 5.482 18 9 18z" fill="#34A853" />
                        <path d="M3.964 10.707c-.18-.54-.282-1.117-.282-1.707 0-.59.102-1.167.282-1.707V4.961H.957C.347 6.175 0 7.55 0 9s.348 2.825.957 4.039l3.007-2.332z" fill="#FBBC05" />
                        <path d="M9 3.58c1.321 0 2.508.454 3.44 1.345l2.582-2.58C13.463.891 11.426 0 9 0 5.482 0 2.438 2.017.957 4.961L3.964 7.29C4.672 5.163 6.656 3.58 9 3.58z" fill="#EA4335" />
                    </svg>
                    Continuar con Google
                </button>

                <p style={{
                    fontSize: '0.75rem',
                    color: '#94a3b8',
                    marginTop: '2rem',
                    lineHeight: '1.5'
                }}>
                    Al iniciar sesión, aceptas los términos y condiciones del servicio.
                </p>
            </div>
        </div>
    );
}
